<template>
  <Root class="home-container">
      <Row>
         
        <i-col span="12">
    <div id="pie"></div>
        </i-col>
        
        <i-col span="12">
    <div id="pie2"></div>
        </i-col>
      </Row>
         <Row>
         
        <i-col span="12">
    <div id="pie3"></div>
        </i-col>
        
        <i-col span="12">
    <div id="pie4"></div>
        </i-col>
      </Row>
      <Row>
         
        <i-col span="12">
    <div id="pie5"></div>
        </i-col>
        
        <i-col span="12">
    <div id="pie6"></div>
        </i-col>
      </Row>
  </Root>
</template>

<script>
//  ECharts 
let echarts = require("echarts/lib/echarts");
// 
require("echarts/lib/chart/pie");
// 
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title"); //  ECharts 
import api from "../network/api";
import { request } from "../network/index";
import Root from "../components/common/Root";
export default {
  name: "home",
  data() {
    return {
      incomeAndTaxdata: [],
      pieCharts: {}
    };
  },
  components: {
    Root
  },
  mounted() {
    this.drawPie();
    this.drawPie2();
    this.drawPie3();
      this.drawPie4();
    this.drawPie5();
    this.drawPie6()
  },
  methods: {
    drawPie2() {
      let params = {};
      this.isShowLoading = true;
      request({
        url: api.tdpreRecordData,
        method: "POST",
        data: params
      })
        .then(res => {
          this.isShowLoading = false;
          this.incomeAndTaxdata = res.data;
          let options = {
            title: {
              text: "" // 
            },

            series: [
              //   type 
              {
                name: "", // 
                type: "pie", // 
                radius: ["0%", "55%"], // 
                data: this.incomeAndTaxdata,
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    },
                    normal:{ 
                        label:{ 
                            show: true, 
                            formatter: '{b} : {c} ({d}%)' 
                        }, 
                        labelLine :{show:true} 
                    } 
                }
              }
            ]
          };
          this.pieCharts = echarts.init(document.getElementById("pie2")); // 
          this.pieCharts.setOption(options); // 
        })
        .catch(res => {
          this.isShowLoading = false;
          this.$Message.error("");
        });
      this.isShowLoading = false;
    },
  drawPie3() {
      let params = {};
